const baseUrl = 'https://pixabay.com/api/';
const keyApi = '&key=12935223-ebb6e80bcfb15004a250fd4eb';
const need = `?image_type=photo&orientation=horizontal&q=что_искать&page=номер_страницы&per_page=12`;

export default {
  page: 1,
  search: '',

  featchPhotos() {
    const requestParams = `?image_type=photo&orientation=horizontal&q=${
      this.search
    }&page=${this.page}&per_page=12&key=12935223-ebb6e80bcfb15004a250fd4eb`;

    return fetch(baseUrl + requestParams)
      .then(response => response.json())
      .then(parsedResponse => {
        this.incrementPage();

        return parsedResponse.hits;
      });
  },

  get searchQuery() {
    this.search;
  },
  set searchQuery(string) {
    this.search = string;
  },

  incrementPage() {
    this.page += 1;
  },
  resetPage() {
    this.page = 1;
  },
};
