import pixabayFatch from './fatch';
import listItemsTemplate from '../../templates/item-photo.hbs';

const refs = {
  formSearch: document.querySelector('#search-form'),
  gallery: document.querySelector('#gallery'),
  btnMoreLoad: document.querySelector('button[data-action="more-photos"]'),
  modal: document.querySelector('#modal'),
  imgInModal: document.querySelector('img'),
};

refs.formSearch.addEventListener('submit', searchFormSubmitHandler);
refs.btnMoreLoad.addEventListener('click', loadMoreBtnHandler);
refs.gallery.addEventListener('click', handleClick);
refs.modal.addEventListener('click', clickOverlayCloseModal);

function searchFormSubmitHandler(e) {
  e.preventDefault();

  const form = e.currentTarget;
  const input = form.elements.search;

  pixabayFatch.searchQuery = input.value;

  clearGallery();
  pixabayFatch.resetPage();
  featchPhotos();

  input.value = '';
}

function loadMoreBtnHandler() {
  featchPhotos();
}

function featchPhotos() {
  pixabayFatch
    .featchPhotos()
    .then(items => {
      console.log(items);

      buildItemsAll(items);
    })
    .catch(error => console.warn(error));
}

function buildItemsAll(items) {
  const markup = listItemsTemplate(items);

  refs.gallery.insertAdjacentHTML('beforeend', markup);
}

function clearGallery() {
  refs.gallery.innerHTML = '';
}

/*
 * MODAL
 */

function handleClick(e) {
  if (e.target.nodeName !== 'BUTTON' && e.target.nodeName !== 'I') return;

  const parent = e.target.closest('.gallery-item');
  const imgInItem = parent.querySelector('img');
  const srcBigPhoto = imgInItem.dataset.source;

  refs.imgInModal.src = srcBigPhoto;

  openModal();
}

function openModal() {
  refs.modal.classList.add('is-visible');
  window.addEventListener('keydown', clickEscape);
}

function clickOverlayCloseModal(e) {
  if (e.target !== e.currentTarget) return;

  closeModal();
}

function closeModal() {
  refs.modal.classList.remove('is-visible');
  window.removeEventListener('keydown', clickEscape);
  refs.imgInModal.src = '';
}

function clickEscape(e) {
  if (e.code !== 'Escape') return;

  closeModal();
}
